  ul.App-navbar {
    margin: 25px;
    margin-left: 5px;
    margin-right: 25px;
    position: sticky;
    padding: 15px;
  }

  li.menuLink {
    list-style-type: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 15px;
    text-align: center;
    font-size: calc(14px + 2vmin);
    text-align: justify;
  }
  
  A { color: white }

  A:link { text-decoration: none; padding: 15px;}

  A:active { text-decoration: none; }

  A:hover { color: black; text-decoration: none; background-color: rgb(197, 197, 197); border-radius: calc(8px + 2vmin); border-color: black; border-width: 1px; }